import React from 'react'
import PropTypes from 'prop-types'


export default function Navbar(props) {
    return (
      
            <>
           
            {/* <nav bar in react */}
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <a className="navbar-brand" href="/">
            <img src="wish.png" alt="WISH" />
          </a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <a className="nav-link active" aria-current="page" href="/">{props.title}</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="Aboutme.js">About me</a>
              </li>
              <li className="nav-item dropdown" />
              <a className="nav-link dropdown-toggle" href="/" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false" >
                Features
              </a>
              <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                <li><a className="dropdown-item" href="/">working</a></li>
                <li><a className="dropdown-item" href="/">working in it</a></li>
                <li><hr className="dropdown-divider" /></li>
                <li><a className="dropdown-item" href="/">available soon</a></li>
              </ul>


            </ul>


            <div class="form-check form-switch" >
              <input class="form-check-input" onClick={props.toggleMode} type="checkbox" id="flexSwitchCheckDefault" />
              <label class="form-check-label " htmlFor="flexSwitchCheckDefault">{props.mode==='dark'?"ðŸŒž":"ðŸŒ›"}</label>
            </div>
            <form className="d-flex">
              <input className="form-control me-2 mx-3" type="search" placeholder="Search" aria-label="Search" />
              <button className="btn btn-outline-success" type="submit">Search</button>
            </form>
          </div>
        </div>
      </nav >
      
            </>
       
    )
}

// prop eg
Navbar.propTypes={
titile:PropTypes.string,}

